 
    export default function () {
    const sliderContainer = document.querySelector('.slider-container');
    const slides = document.querySelectorAll('.slide');
    const navButtons = document.querySelectorAll('.nav-btn');
    const dotsContainer = document.querySelector('.dots-container');
    const dots = document.querySelectorAll('.dot');
    const totalSlides = slides.length;
    let currentSlide = 0;
    let slideInterval ;

   2 // Função para mostrar o slide atual
    function showSlide(index) {
        // Remove a classe 'active' de todos os slides e pontos
        slides.forEach(slide => slide.classList.remove('active'));
        dots.forEach(dot => dot.classList.remove('active'));

        // Adiciona a classe 'active' ao slide e ponto corretos
        slides[index].classList.add('active');
        dots[index].classList.add('active');
    }

    // Função para ir para o próximo slide
    function nextSlide() {
        currentSlide = (currentSlide + 1) % totalSlides;
        showSlide(currentSlide);
    }

    // Função para iniciar a transição automática
    function startAutoSlide() {
        slideInterval = setInterval(nextSlide, 5000); // Muda de slide a cada 5 segundos
    }

    // Função para parar a transição automática
    function stopAutoSlide() {
        clearInterval(slideInterval);
    }

    // Inicializa o slider
    showSlide(currentSlide);
    startAutoSlide();

    // Adiciona eventos de clique aos botões de navegação
    navButtons.forEach(button => {
        button.addEventListener('click', () => {
            stopAutoSlide(); // Para o autoplay
            const isNext = button.classList.contains('next');
            if (isNext) {
                currentSlide = (currentSlide + 1) % totalSlides;
            } else {
                currentSlide = (currentSlide - 1 + totalSlides) % totalSlides;
            }
            showSlide(currentSlide);
            startAutoSlide(); // Reinicia o autoplay após a navegação manual
        });
    });

    // Adiciona eventos de clique aos pontos indicadores
    dots.forEach((dot, index) => {
        dot.addEventListener('click', () => {
            stopAutoSlide(); // Para o autoplay
            currentSlide = index;
            showSlide(currentSlide);
            startAutoSlide(); // Reinicia o autoplay
        });
    });

    // Para o autoplay quando o mouse passa sobre o slider
    sliderContainer.addEventListener('mouseenter', stopAutoSlide);
    sliderContainer.addEventListener('mouseleave', startAutoSlide);
};
 